@import "../variables.scss";
@import "../mixins.scss";
@import "../global.scss";
@import "../alert.scss";

* {
    margin : 0;
    padding :  0;
    box-sizing : border-box;
    font-family: "PT Sans",sans-serif;
}


a {
    text-decoration: none;
    color : inherit;                   
}

#content  {
    line-height: 1.3;
    min-height: 60vh;
    display: flex;
    flex-wrap : wrap;
    justify-content: space-between;
    @include respond("mobile") {
        flex-direction: column;
    }


    .action-icon {
        margin-left : .5rem;
    }

    .item-link {
        display: flex;
        flex-direction: column;
        cursor: pointer;
    }

    .container-left  {
        position: relative;
        flex : 0 0 40%;
        display: flex;
        flex-direction: column;
        flex-wrap : wrap;
        padding : 1rem 2rem;
    
        color : white;
        margin: 1.5rem 2rem 2rem;
        border-radius: 10px;
        // box-shadow: 0px 3px 10px rgba(36, 36, 36, 0.664);
    }

    .main-title {
        position: relative;
        margin-top : 2rem;
        margin-left : 2rem;
        flex :  0 0 100%;
        font-size : 2rem;
        color : #303030;
    }

    .main-title::after {
        content : "";
        margin-top : .5rem;
        display: block;
        width: 14rem;
        background-color : $base-color-dark-pink;
        height : 4px;
    }


    ul {
        list-style-type: none;

        li {
            color : white;

            button {
                color : inherit;
            }
        }
    }

    .main-profile {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items : center;
        text-align: center;
        font-weight: 600;
        font-size : 1rem;
        color : white;


        .email .item-title,
        .age .item-title,
        .balance .item-title {
            color :#303030;
            font-size : 1.2rem;
            text-align : left;
            margin-top : 1rem;
            margin-bottom : .5rem;
        }


        .email .item-value,
        .age .item-value,
        .balance .item-value {
            padding : .4rem .9rem;
            background-color: #303030;
            margin-bottom : 1rem;
            margin-right : 1rem;
            border-radius: 10px;
            box-shadow: 0px 2px 7px rgba(36, 36, 36,  0.425);
        }

        .greet-user {
            display: flex;
            align-items: center;
            font-size : 1.2rem;
            flex :  0 0  100%;
            text-align: left;
            margin : 1rem 0rem;
            margin-right : 1rem;
            border-radius: 10px;
            color : #303030;
            

            .top-up-btn {
                margin-left : .5rem;
                padding : .4rem .9rem;
                border : none;
                text-decoration: none;
                outline : none;
                border-radius : 10px;
                background-color : $base-color-pink;
                color : white;
                transform : scale(1);
                transition: all .3s ease;
            }

            .top-up-btn:hover {
                transform : scale(1.03);
                background-color : $base-color-dark-pink;
            }
        }

        .favorite-tag-list {
            position: relative;
            flex : 0 0 100%;
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            justify-content: flex-start;

            li {    
                margin-top : 1rem;
                height: 30px;

                a,
                button {
                    margin-bottom : 1rem;
                    font-size : 1rem;
                    margin-right : 1rem;
                    padding : .4rem .9rem;
                    border-radius: 10px;
                    background-color : #278acc;
                    text-align: center;
                    box-shadow: 0px 2px 7px rgba(36, 36, 36,  0.425);
    
                }
            }



            .favorite-tags {
                margin-bottom: .5rem;
                font-size : 1.2rem;
                padding : 0;
                font-weight: 600;
                width: 100%;
                color: #303030;
                background-color: transparent;
                text-align: left;
                box-shadow: none;
            } 


        }
    }

    .action-profile {
        margin-top : 3rem;
        display: flex;
        flex-wrap : wrap;



        .item-link {
            margin-right : 1rem;
            display : block;
            margin-bottom : 1.5rem;
            font-weight: 600;
            font-size : 1rem;
            padding : .4rem .9rem;
            border : none;
            outline : none;
            background-color : #25a14e;
            border-radius: 10px;
            box-shadow: 0px 2px 7px rgba(36, 36, 36, 0.425);
        }
    }


    .container-right {
        position: relative;
        flex : 0 0 45%;
        max-height: 550px;
        overflow: hidden;
        background-color: $base-color-pink;
        padding : 2rem;
        margin: 1.5rem 2rem 2rem;
        border-radius : 10px;
        box-shadow: 0 2px 10px rgba(27, 27, 27, 0.692);

        .title-content {
            color : white;
            margin-bottom: 1.5rem;
            margin-top : .8rem;
        }

        .show-content {
            position: relative;
            width: 100%;
            height: 100%;

            .alert {
                font-weight: 600;
                font-size : 1.3rem;
                text-align: left;
                padding-bottom: 1rem;
                border-radius : 10px;
            }



            .btn-submit {
                padding : .5rem 1.4rem;
                border : none;
                outline : none;
                color : white;
                border-radius : 5px;
                font-weight : 600;
                letter-spacing : 3px;
                background-image : linear-gradient(270deg,#e25f90,#a068d3);
                box-shadow : 2px 1px 4px rgba(34, 34, 34, 0.342);
                transform : scale(1);
                transition : .2s ease;
            }


            .intro-content {
                width: 100%;
                height : 100%;
                display: flex;
                flex-direction : column;
                justify-content : flex-start;
                color : white;

                h2,
                p,
                button {
                    margin : .8rem 0rem;
                }



                p {
                    font-size : 1rem;
                }
            }

            .content-wrapper {
                display: block;
                position : relative;
                left : 0;
                top: 0;
                padding : 0rem 1rem;
                transform: translate(0,0);
                transition: all 1s ease;
     
            }

            .input-group {
                color : white;
                width : 100%;
                margin-bottom : 1rem;

                label {
                    display : block;
                    margin-bottom: .5rem;
                }

                input {
                    padding : .3rem .5rem;
                    background-color : #fefefefe;
                    border : none;
                    outline : none;
                    border-radius : 10px;
                    width : 100%;
                    max-width : 20rem;
                }

                .error-input {
                    margin-top : .5rem;
                    color : #303030;
                    font-weight: 600;
                    font-size : 1.1rem;
                }
            }


            .add-tag-wrapper {

                .tag-items-wrapper {
                    display: flex;
                    flex-wrap : wrap;
                    margin-bottom: 1rem;
        
                    .tag-item {
                        display : flex;
                        align-items: center;
                        margin : 0rem .6rem .6rem 0;
                        background-color : #329999;
                        border-radius : 5px;
                        padding : .4rem .9rem;
    
                        .btn-delete-tag {
                            margin-left : .5rem;
                        }
    
                    }

                    .btn-delete-tag {
                        cursor : pointer;
                    }
    
                }
    
                .tag-selector-wrapper {
                    margin-bottom: 1rem;
    
                    select {
                        display: block;
                        width : 100%;
                        padding : .3rem .5rem;
                        border-radius : 10px;
                        border : none;
                        outline : none;
                    }
                }



            }


            #form-change-password {
                display: flex;
                flex-direction : column;
                align-items: flex-start;

                #error-change-password {
                    color :#303030;
                    font-weight : 600;
                    font-size : 1.2rem;
                    margin-bottom : 1rem;
                }
            }
        }



        
        .hide-wrapper {
            display: none;
            opacity: 0;
            transform: translate(0,-200px);
        }

        


    }



}